.page-footer {
  background-color: #041827;
  flex: 0 0 auto;
  height: 100%;
}

.page-footer__logo h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #f2f5f7;
  margin: 0;
}

.page-footer__logo img {
  @media (max-width: $mobile-width) {
    min-height: 55px;
  }
}

.page-footer__logo span {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #f8fdffb3;
}

.page-footer__logo {
  cursor: pointer;
}

.page-footer__logo:hover {
  opacity: 0.7;
}

.page-footer__logo:active {
  opacity: 0.5;
}

.page-footer__logo {
  display: flex;
  width: 215px;
  height: 55px;

  @media (max-width: $mobile-width) {
    margin: 0 auto;
  }
}

.page-footer__logo-descript {
  margin-left: 15px;
}

.page-footer__logo-text {
  margin-right: 145px;
  flex-shrink: 0;
  width: 215px;

  @media (min-width: $desktop-min) and (max-width: $desktop-max) {
    margin-right: auto;
  }

  @media (min-width: $desktop-min) and (max-width: $desktop-resize) {
    margin-right: 40px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 1220px;
  padding: 50px 0 55px;
  margin: 0 auto;
  box-sizing: content-box;

  @media (min-width: $desktop-min) and (max-width: $desktop-max) {
    max-width: 1220px;
    width: 95%;
    padding: 50px 0 55px;
    gap: 30px;
  }

  @media (max-width: $tablet-width) {
    width: 87%;
    padding: 50px 0 65px;
  }

  @media (max-width: $mobile-width) {
    width: auto;
    padding: 35px 0;
    flex-direction: column;
    row-gap: 0;
  }
}

.page-footer__wrapper-logo-social {
  margin-right: 159px;
  flex-shrink: 0;

  @media (min-width: $desktop-min) and (max-width: $desktop-max) {
    margin-right: auto;
  }

  @media (max-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    margin-right: 0;
    margin-bottom: 23px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    align-self: center;
    margin-bottom: 25px;
    padding: 0 10px;
  }
}

.page-footer__logo-wrapper {
  margin-bottom: 30px;
  align-self: flex-start;
  flex-shrink: 0;

  @media (max-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    margin-right: 5px;
    margin-bottom: 13px;
    align-self: center;
  }
}

.page-footer__nav {
  margin-right: 125px;
  padding-top: 8px;

  @media (min-width: $desktop-min) and (max-width: $desktop-max) {
    margin-right: auto;
  }

  @media (max-width: $tablet-width) {
    margin-right: 148px;
  }

  @media (min-width: $tablet-resize) and (max-width: $tablet-width) {
    margin-right: auto;
  }

  @media (max-width: $mobile-width) {
    margin: 0;
    border-top: 1px solid #f8fdffb3;
    border-bottom: 1px solid #f8fdffb3;
    padding: 21px 0 22px;
  }
}

.page-footer__nav h4,
.page-footer__contacts-wrapper h4 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 23px;
  letter-spacing: 0.8px;

  @media (max-width: $mobile-width) {
    margin: 0;
    padding: 0 41px 0 24px;
  }
}

.page-footer__nav-list {
  display: grid;
  grid-template-columns: repeat(2, auto);
  row-gap: 9px;
  column-gap: 49px;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: $tablet-width) {
    grid-template-columns: auto;
  }

  @media (max-width: $mobile-width) {
    padding: 0 20px;
    margin-top: 15px;
  }
}

.page-footer__nav-item {
  @media (min-width: $desktop-min) {
    &--montage {
      order: 5;
    }

    &--contacts {
      order: 4;
    }

    &--production {
      order: 3;
    }

    &--products {
      order: 2;
    }

    &--delivery {
      order: 2;
    }
  }
}

.page-footer__nav-link {
  text-decoration: none;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffffb3;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__contacts-wrapper {
  padding-top: 8px;

  @media (max-width: $tablet-width) {
    width: 47%;
    max-width: 357px;
  }

  @media (max-width: $mobile-width) {
    width: auto;
    max-width: none;
    border-bottom: 1px solid #f8fdffb3;
    padding: 22px 0;
  }
}

.page-footer__contacts-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 5px;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: $mobile-width) {
    padding: 0 25px;
    margin-top: 15px;
    margin-bottom: -8px;
  }
}

.page-footer__contacts-item {
  min-width: 170px;
}

.page-footer__contacts-address {
  color: #ffffffb3;
  margin-bottom: 2px;

  @media (max-width: $mobile-width) {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 12px;
    margin-top: 2px;
  }
}

.page-footer__phone-link {
  color: #ffffffb3;
  text-decoration: none;
  line-height: 25px;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }

  @media (max-width: $mobile-width) {
    font-size: 14px;
  }
}
